@use "../abstracts" as *;

.button {
  height: 50px;
  padding: 0 25px;
  background: $gradiant-color-1;
  border-radius: 25px;
  color: white;
  font-size: $fontsize_2;
  font-weight: 500;
  box-shadow: 0 4px 10px 0px #00000040;
  &:hover {
    box-shadow: 0px 4px 15px 0px #00000059;
    opacity: 0.85;
  }
}
